.header {
	display: flex;
	justify-content: space-between;
	font-size: 1.1rem;
}

.logo {
	width: 10rem;
	height: 3rem;
}

.header-menu {
	list-style: none;
	display: flex;
	gap: 2.5rem;
	color: white;
}

.header-menu > li:hover {
	color: var(--orange);
	cursor: pointer;
}

/* Mobile Responsive */
@media screen and (max-width: 768px) {
	.header > :nth-child(2) {
		position: fixed;
		right: 2rem;
		z-index: 99;
	}
	.header-menu {
		flex-direction: column;
		background-color: var(--appColor);
		background-image: url(data:image/png;base64,);

		padding: 2rem;
		font-size: 1.5rem;
		border-radius: 1rem;
	}
}
